.header-color {
  background: #833ab4;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.landing-grid{
  background: #833ab4;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
text-align: center;
position: fixed;
top: 0%;
left: 0%;
height: 100%;
width: 100%;
}
.avatar-img{
  height:10%;
  padding-top: 5em;
}
.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 2%;
}
.banner-text h1{
  color: white;
  font-weight: bold;
  font-size:66px;
}
.banner-text hr{
  border-top:5px dotted white;
  width: 50%;
  margin: auto;
}
.banner-text p{
  color:white;
  font-size: 20px;
  padding: 1em;
}
.social-links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin:auto;
  }
  .social-links i{
    color:white;
    font-size: 5em;
    padding: 8px;
  }
  .project-grid{
    display: flex;
  }
  .contact-body{
    margin: auto;
   
    position: fixed;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top: 2em; 
  }
  .contact-grid{
    text-align: center;
    justify-content: center;
    width: 80%;
    height: 550px;
    background: white;
    color: black;
    border: 2px solid red;
    margin-bottom: 20px;
  }
  .contact-grid h2{
    font-family: 'Anton', sans-serif;
  }
  .contact-grid hr{
    border-top: 5px dotted black;
    width: 50%;
    margin: auto;
  }
  .contact-list i{
    font-size:66px;
    padding-right: 5rem;
  }
  .aboutme-right-col{
    background: #27221f;
    color: white;
    padding: 1em;
  }
  .skill{
    color: black;
    
  }
